<template>
    <div class="navigational">
        <navigate backgroundColor="#3899D7" color="#fff" position="absolute" title="航道信息"/>
        <div :style="{maxHeight: styles.itemsWrapper.maxHeight + 'px'}" class="items-wrapper" ref="itemsWrapper">
            <div :style="{
                                width: styles.items.width + 'px',
                                transform: `translate3d(${styles.items.scrollLeft}px, 0, 0)`
                         }"
                 @touchend="onTouchend"
                 @touchmove="onTouchmove"
                 @touchstart="onTouchstart"
                 class="items"
                 ref="items">
                <div :data-id="item.typeId" :style="{width: styles.item.width + 'px'}"
                     @click="handlerNavigationItem(item.typeId)"
                     class="items-item"
                     ref="item"
                     v-for="(item, index) in searchList">
                    <img :class="params.typeId === item.typeId ? 'mu-error-color' : 'mu-primary-color'"
                         :src="item.typeId | formatFind(icons, 'value', 'typeId')">
                    <span :class="params.typeId === item.typeId ? 'mu-error-text-color' : ''">{{item.value}}</span>
                </div>
            </div>
        </div>
        <transition name="fade-shelter">
            <div class="search-box-wrapper aberration-wrapper-color" v-show="params.typeId">
                <div class="search-box aberration-color">
                    <mu-icon color="#ccc" size="22" value="search"/>
                    <mu-text-field class="demo-divider-form" full-width placeholder="请输入搜索内容" solo
                                   v-model="params.name"/>
                </div>
            </div>
        </transition>
        <div class="scroll-wrapper"
             ref="content"
             v-show="params.typeId">
            <mu-load-more :loading="loading"
                          :refreshing="refreshing"
                          @load="load"
                          @refresh="onRefresh"
                          v-show="list.length">
                <mu-list>
                    <mu-list-item :key="item.id" class="search-list-item border-1px-b"
                                  v-for="(item, index) in list">
                        <mu-list-item-action :style="{minWidth: item.typeName.length + 2 + 'em'}">
                            <span class="theme-text-sub-color">【{{item.typeName}}】</span>
                        </mu-list-item-action>
                        <mu-list-item-content>
                            <mu-list-item-title>{{item.name}}</mu-list-item-title>
                        </mu-list-item-content>
                        <mu-list-item-action style="min-width: 95px">
                            <mu-button @click="handlerListItem(item)" class="button"
                                       color="#3899D7"
                                       flat small>查看
                            </mu-button>
                        </mu-list-item-action>
                    </mu-list-item>
                </mu-list>
            </mu-load-more>
            <no-success text="暂未查询到数据" v-show="!list.length"/>
        </div>


        <transition class="views-left">
            <keep-alive>
                <router-view class="views"/>
            </keep-alive>
        </transition>

    </div>
</template>

<script>
    import Navigate from "../../base/navigate/navigate"
    import NoSuccess from "../../base/no-success/no-success"
    import {getNavigationals} from "../../api/navigation"
    import {debounce, isArray} from "../../types/index"
    import {appMixins, searchMixins} from "../../common/mixins"

    export default {
        name: "navigational",
        mixins: [appMixins, searchMixins],
        data() {
            return {
                /***
                 * 上拉刷新&下拉加载
                 */
                refreshing: false,
                loading: false,
                /***
                 * 结果列表
                 */
                list: [],
                /***
                 * 搜索条件
                 */
                params: {
                    typeId: '',
                    name: '',
                    pageNumber: 1,
                    pageSize: 20
                },
                /***
                 * 导航栏样式
                 */
                styles: {
                    itemsWrapper: {
                        width: undefined,
                        maxHeight: 500
                    },
                    items: {
                        width: undefined,
                        scrollLeft: 0
                    },
                    item: {
                        width: undefined
                    }
                }
            }
        },
        created() {
            this.$watch('params.name', debounce(value => {
                this.onRefresh()
            }, 300))

            this.$watch('params.typeId', value => {
                this.$router.replace({
                    name: 'navigational',
                    query: {
                        id: value
                    }
                })

                this.onRefresh()
            })

            const {width} = this.screen

            this.styles.itemsWrapper.width = width
            this.styles.items.width = width
            this.styles.item.width = width * 0.2
        },
        activated() {
            this.params.name = ''

            if (this.$route.query.id) {
                this.handlerNavigationItem(this.$route.query.id)
            }
        },
        methods: {
            // 点击导航
            handlerNavigationItem(id) {
                const {width} = this.screen

                if (this.params.typeId === id) {
                    this.params.typeId = ''
                    this.styles.item.width = width * 0.2
                    this.styles.items.width = width
                    this.styles.items.scrollLeft = 0
                    this.styles.itemsWrapper.maxHeight = 500
                } else {
                    this.params.typeId = id

                    this.styles.item.width = 0.2 * width
                    this.styles.items.width = this.searchList.length * 0.2 * width
                    this.styles.itemsWrapper.maxHeight = 90

                    this.animation()
                }

                this.params.name = ''
            },
            // 刷新
            onRefresh() {
                this.refreshing = true
                this.$refs.content.scrollTop = 0

                this.getNavigationals()
            },
            // 加载更多
            load() {
                this.loading = true
                this.params.pageNumber++

                if (this.params.typeId) {
                    this.params.types = [this.params.typeId]

                    getNavigationals(this.params).then(res => {
                        this.list.push(...isArray(res))

                        this.loading = false
                    })
                }
            },
            // 获取指定助航信息
            getNavigationals() {
                this.params.pageNumber = 1

                if (this.params.typeId) {
                    this.params.types = [this.params.typeId]

                    getNavigationals(this.params).then(res => {
                        this.list = isArray(res)

                        this.refreshing = false

                        this.$toast.clear()
                    })
                } else {
                    this.list = []
                }
            },
            /***
             * 查询详情
             */
            handlerListItem(item) {
                this.$router.push({
                    name: 'navigational-details',
                    params: {
                        type: item.subType || item.typeId || this.$route.query.id || this.params.typeId,
                        id: item.id
                    },
                    query: {
                        typeId: item.typeId
                    }
                })
            },
            /***
             * 动画
             */
            animation() {
                clearTimeout(this.timer)

                const index = this.searchList.findIndex(item => {
                    return this.params.typeId === item.typeId
                })

                if (index === -1) {
                    return
                }

                const width = this.screen.width
                const max = this.styles.items.width - width

                let current = index * (width * 0.2) - (width * 0.8) / 2

                if (current < 0) {
                    current = 0
                }

                if (current > max) {
                    current = max
                }

                this.styles.items.scrollLeft = -current
            },
            /***
             * 滚动动画
             * @param event
             */
            onTouchstart(event) {
                clearTimeout(this.timer)
                this.$refs.items.style.transition = 'none'
                this.scrollX = event.touches[0].pageX
            },
            onTouchmove(event) {
                let current = event.touches[0].pageX - this.scrollX + this.styles.items.scrollLeft

                const MAX = this.styles.items.width - this.styles.itemsWrapper.width
                const MIN = 0

                if (current > MIN) {
                    current = MIN
                }

                if (current < -MAX) {
                    current = -MAX
                }

                this.styles.items.scrollLeft = current

                this.scrollX = event.touches[0].pageX
            },
            onTouchend() {
                this.$refs.items.style.transition = 'all 1s'
                this.timer = setTimeout(this.animation, 2000)
            }
        },
        components: {
            NoSuccess,
            Navigate
        }
    };
</script>

<style lang="stylus" scoped>
    @import '../../common/stylus/variable.styl';

    .navigational
        padding-top 46px
        width 100%
        height 100%
        background-color #fff
        overflow hidden

        .items-wrapper
            width 100%
            max-height 1000px
            overflow hidden
            transition all 1s

            .items
                display flex
                flex-wrap wrap
                height 100%
                overflow hidden
                transition all 1s

                .items-item
                    display flex
                    justify-content center
                    align-items center
                    flex-direction column
                    width 20%
                    height 90px
                    transition all 1s

                    > img
                        display block
                        padding 8px
                        width 40px
                        height 40px
                        border-radius 50%
                        transition all 1s

                    > span
                        margin-top .5em
                        font-size $font-size-ms

        .search-box-wrapper
            overflow hidden

            .search-box
                position relative
                margin 10px

                .mu-icon
                    position absolute
                    left 10px
                    top 6px

                .mu-input
                    padding 0 10px 0 40px
                    min-height 32px
                    font-size $font-size-m

        .mu-load-more
            min-height 100%

            .button
                border 1px solid #3899D7
                border-radius 5px
</style>



